.game-row {
  display: grid;
  grid-template-columns: auto 1fr auto; 
  align-items: center;
  gap: 8px;
  padding: 0.75rem 0.25rem;
  border-bottom: 1px solid #e5e5e5;
  cursor: pointer;
}

.team-left {
  display: flex;
  align-items: center;
  gap: 6px;
}

.team-right {
  display: flex;
  align-items: center;
  gap: 6px;
  justify-content: flex-end; 
}

.game-center {
  text-align: center;
  font-size: 0.95rem;
  line-height: 1.2rem;
  word-wrap: break-word;
}

.team-logo {
  width: 32px;
  height: 32px;
  object-fit: contain;
}

.detail-team-logo {
  width: 100px;
  height: 100px;
}

.detail-game-row {
  display: grid;
  grid-template-columns: auto 1fr auto; 
  align-items: center;
  gap: 8px;
  padding: 0.75rem 0.25rem;
  cursor: pointer;
}

